

























































































import { Component, Vue } from 'vue-property-decorator';
import Page from '@/components/Page.vue';
import copy from 'copy-to-clipboard';

export interface IColor {
  name: string;
  class: string;
  variable: string;
}

export interface IColors {
  id: string;
  colors: IColor[];
}

@Component({
  components: { Page },
})
export default class Color extends Vue {
  private copiedVariable: IColor | null = null;

  private readonly colors: IColors[] = [
    {
      id: 'white-black',
      colors: [
        {
          name: 'Static white',
          class: 'white',
          variable: '--static-white',
        }, {
          name: 'Static black',
          class: 'black',
          variable: '--static-black',
        },
      ] as IColor[],
    },
    {
      id: 'grey',
      colors: [
        {
          name: 'Grey 50',
          class: 'grey-50',
          variable: '--grey-50',
        }, {
          name: 'Grey 75',
          class: 'grey-75',
          variable: '--grey-75',
        }, {
          name: 'Grey 100',
          class: 'grey-100',
          variable: '--grey-100',
        }, {
          name: 'Grey 200',
          class: 'grey-200',
          variable: '--grey-200',
        }, {
          name: 'Grey 300',
          class: 'grey-300',
          variable: '--grey-300',
        }, {
          name: 'Grey 400',
          class: 'grey-400',
          variable: '--grey-400',
        }, {
          name: 'Grey 500',
          class: 'grey-500',
          variable: '--grey-500',
        }, {
          name: 'Grey 600',
          class: 'grey-600',
          variable: '--grey-600',
        }, {
          name: 'Grey 700',
          class: 'grey-700',
          variable: '--grey-700',
        }, {
          name: 'Grey 800',
          class: 'grey-800',
          variable: '--grey-800',
        }, {
          name: 'Grey 900',
          class: 'grey-900',
          variable: '--grey-900',
        },
      ] as IColor[],
    },
    {
      id: 'blue',
      colors: [
        {
          name: 'Static blue 400',
          class: 'blue-400',
          variable: '--static-blue-400',
        }, {
          name: 'Blue 500',
          class: 'blue-500',
          variable: '--blue-500',
        }, {
          name: 'Blue 600',
          class: 'blue-600',
          variable: '--blue-600',
        }, {
          name: 'Blue 700',
          class: 'blue-700',
          variable: '--blue-700',
        },
      ] as IColor[],
    },
    {
      id: 'red',
      colors: [
        {
          name: 'Static red 400',
          class: 'red-400',
          variable: '--static-red-400',
        }, {
          name: 'Red 500',
          class: 'red-500',
          variable: '--red-500',
        }, {
          name: 'Red 600',
          class: 'red-600',
          variable: '--red-600',
        }, {
          name: 'Red 700',
          class: 'red-700',
          variable: '--red-700',
        },
      ] as IColor[],
    },
    {
      id: 'orange',
      colors: [
        {
          name: 'Static orange 400',
          class: 'orange-400',
          variable: '--static-orange-400',
        }, {
          name: 'Orange 500',
          class: 'orange-500',
          variable: '--orange-500',
        }, {
          name: 'Orange 600',
          class: 'orange-600',
          variable: '--orange-600',
        }, {
          name: 'Orange 700',
          class: 'orange-700',
          variable: '--orange-700',
        },
      ] as IColor[],
