          name: 'Static green 400',
          class: 'green-400',
          variable: '--static-green-400',
        }, {
          name: 'Green 500',
          class: 'green-500',
          variable: '--green-500',
        }, {
          name: 'Green 600',
          class: 'green-600',
          variable: '--green-600',
        }, {
          name: 'Green 700',
          class: 'green-700',
          variable: '--green-700',
        },
      ] as IColor[],
    },
  ];

  private clickToCopy(color: IColor) {
    copy(color.variable);

    this.copiedVariable = color;
    window.setTimeout(() => {
      this.copiedVariable = null;
    }, 500);
  }
}
